import { api, APIError } from "encore.dev/api";
import { authDB } from "./db";
import type { GoogleOAuthRequest, AuthResponse, User, Organization } from "./types";
import { 
  generateGoogleOAuthUrl, 
  exchangeGoogleAuthCode, 
  getGoogleUserProfile,
  generateAccessToken, 
  generateRefreshToken,
  generateRandomState,
  isDataModeMock 
} from "./utils";
import { mockUsers, mockOrganizations, findOrganizationById } from "./mock-data";

// Initiates Google OAuth login flow
export const googleLogin = api<void, { redirect_url: string; state: string }>(
  { expose: true, method: "GET", path: "/api/v1/auth/google/login" },
  async () => {
    const state = generateRandomState();
    const redirectUrl = generateGoogleOAuthUrl(state);
    
    return {
      redirect_url: redirectUrl,
      state
    };
  }
);

// Handles Google OAuth callback
export const googleCallback = api<GoogleOAuthRequest, AuthResponse>(
  { expose: true, method: "POST", path: "/api/v1/auth/google/callback" },
  async (req) => {
    if (!req.auth_code) {
      throw APIError.invalidArgument("Authorization code is required");
    }

    try {
      // Exchange authorization code for tokens
      const tokenResponse = await exchangeGoogleAuthCode(req.auth_code);
      
      // Get user profile from Google
      const googleProfile = await getGoogleUserProfile(tokenResponse.access_token);
      
      if (!googleProfile.email || !googleProfile.verified_email) {
        throw APIError.invalidArgument("Email verification required");
      }

      if (isDataModeMock()) {
        // Mock mode - find or create user in mock data
        let user = mockUsers.find(u => u.email === googleProfile.email);
        
        if (!user) {
          // Create new mock user with default organization
          const defaultOrg = mockOrganizations[0];
          user = {
            id: `google-mock-${Date.now()}`,
            email: googleProfile.email,
            first_name: googleProfile.given_name,
            last_name: googleProfile.family_name,
            org_id: defaultOrg.id,
            role: "viewer", // Default role for OAuth users
            created_at: new Date(),
            updated_at: new Date(),
            is_active: true
          };
          mockUsers.push(user);
        }

        // Get user's organization
        const organization = findOrganizationById(user.org_id) || mockOrganizations[0];

        const accessToken = generateAccessToken(user.id, user.email);
        const refreshToken = generateRefreshToken(user.id, user.email);

        return {
          access_token: accessToken,
          refresh_token: refreshToken,
          user,
          organization,
          expires_in: 3600
        };
      }

      // Live mode - database operations
      let user: User | null;

      // Check if user exists
      user = await authDB.queryRow<User>`
        SELECT id, email, first_name, last_name, org_id, role, created_at, updated_at, last_login, is_active
        FROM users 
        WHERE email = ${googleProfile.email}
      `;

      let organization: Organization;

      if (!user) {
        // Find or create default organization for new OAuth users
        let defaultOrg = await authDB.queryRow<Organization>`
          SELECT id, name, created_at, updated_at FROM organizations WHERE name = 'Default Organization'
        `;
        
        if (!defaultOrg) {
          defaultOrg = await authDB.queryRow<Organization>`
            INSERT INTO organizations (name)
            VALUES ('Default Organization')
            RETURNING id, name, created_at, updated_at
          `;
        }
        
        if (!defaultOrg) {
          throw APIError.internal("Failed to create default organization");
        }
        
        organization = defaultOrg;

        // Create new user from Google profile
        user = await authDB.queryRow<User>`
          INSERT INTO users (email, first_name, last_name, org_id, role, is_active)
          VALUES (${googleProfile.email}, ${googleProfile.given_name || null}, ${googleProfile.family_name || null}, ${organization.id}, 'viewer', true)
          RETURNING id, email, first_name, last_name, org_id, role, created_at, updated_at, last_login, is_active
        `;

        if (!user) {
          throw APIError.internal("Failed to create user from Google profile");
        }
      } else {
        // Get existing user's organization
        const existingOrg = await authDB.queryRow<Organization>`
          SELECT id, name, created_at, updated_at FROM organizations WHERE id = ${user.org_id}
        `;
        
        if (!existingOrg) {
          throw APIError.internal("User's organization not found");
        }
        
        organization = existingOrg;
        
        // Update last login
        await authDB.exec`
          UPDATE users 
          SET last_login = NOW(), updated_at = NOW()
          WHERE id = ${user.id}
        `;
      }

      const accessToken = generateAccessToken(user.id, user.email);
      const refreshToken = generateRefreshToken(user.id, user.email);

      return {
        access_token: accessToken,
        refresh_token: refreshToken,
        user,
        organization,
        expires_in: 3600
      };

    } catch (error) {
      if (error instanceof APIError) {
        throw error;
      }
      console.error("Google OAuth callback error:", error);
      throw APIError.internal("OAuth authentication failed");
    }
  }
);