import { api, APIError } from "encore.dev/api";
import { Header } from "encore.dev/api";
import { authDB } from "./db";
import type { User, Organization } from "./types";
import { verifyAccessToken, isDataModeMock } from "./utils";
import { mockUsers, findOrganizationById } from "./mock-data";

// Gets current user information from access token
export const getCurrentUser = api<void, { user: User; organization: Organization }>(
  { expose: true, method: "GET", path: "/api/v1/auth/me" },
  async (req) => {
    // Extract Bearer token from Authorization header
    const authHeader = Header("Authorization");
    if (!authHeader || !authHeader.startsWith("Bearer ")) {
      throw APIError.unauthenticated("Authorization header required");
    }

    const token = authHeader.substring(7); // Remove "Bearer " prefix
    
    try {
      // Verify and decode the access token
      const payload = verifyAccessToken(token);
      
      if (isDataModeMock()) {
        // Mock mode - find user in mock data
        const user = mockUsers.find(u => u.id === payload.user_id);
        if (!user) {
          throw APIError.unauthenticated("User not found");
        }
        
        const organization = findOrganizationById(user.org_id);
        if (!organization) {
          throw APIError.internal("User's organization not found");
        }
        
        return { user, organization };
      }

      // Live mode - get user from database
      const user = await authDB.queryRow<User>`
        SELECT id, email, first_name, last_name, org_id, role, created_at, updated_at, last_login, is_active
        FROM users 
        WHERE id = ${payload.user_id} AND is_active = true
      `;

      if (!user) {
        throw APIError.unauthenticated("User not found or inactive");
      }

      // Get user's organization
      const organization = await authDB.queryRow<Organization>`
        SELECT id, name, created_at, updated_at FROM organizations WHERE id = ${user.org_id}
      `;
      
      if (!organization) {
        throw APIError.internal("User's organization not found");
      }

      return { user, organization };

    } catch (error) {
      if (error instanceof APIError) {
        throw error;
      }
      console.error("Session validation error:", error);
      throw APIError.unauthenticated("Invalid session");
    }
  }
);

// Logout endpoint (invalidates session client-side)
export const logout = api<void, { success: boolean }>(
  { expose: true, method: "POST", path: "/api/v1/auth/logout" },
  async () => {
    // Since we're using stateless JWTs, logout is primarily handled client-side
    // by removing the tokens. In a production environment, you might want to
    // implement a token blacklist for enhanced security.
    
    return { success: true };
  }
);