import * as crypto from "crypto";
import * as jwt from "jsonwebtoken";
import { secret } from "encore.dev/config";
import { APIError } from "encore.dev/api";
import type { TokenPayload, GoogleTokenResponse, GoogleUserProfile } from "./types";

const jwtSecret = secret("JWT_SECRET");
const jwtRefreshSecret = secret("JWT_REFRESH_SECRET");
const dataMode = secret("DATA_MODE");
const googleClientId = secret("GOOGLE_CLIENT_ID");
const googleClientSecret = secret("GOOGLE_CLIENT_SECRET");
const googleRedirectUri = secret("GOOGLE_REDIRECT_URI");

export function hashPassword(password: string): string {
  const salt = crypto.randomBytes(16).toString("hex");
  const hash = crypto.pbkdf2Sync(password, salt, 10000, 64, "sha512").toString("hex");
  return `${salt}:${hash}`;
}

export function verifyPassword(password: string, hashedPassword: string): boolean {
  const [salt, hash] = hashedPassword.split(":");
  const verifyHash = crypto.pbkdf2Sync(password, salt, 10000, 64, "sha512").toString("hex");
  return hash === verifyHash;
}

export function generateAccessToken(userId: string, email: string): string {
  const payload: TokenPayload = {
    user_id: userId,
    email,
    exp: Math.floor(Date.now() / 1000) + (60 * 60), // 1 hour
    iat: Math.floor(Date.now() / 1000)
  };
  
  return jwt.sign(payload, jwtSecret(), { algorithm: "HS256" });
}

export function generateRefreshToken(userId: string, email: string): string {
  const payload: TokenPayload = {
    user_id: userId,
    email,
    exp: Math.floor(Date.now() / 1000) + (60 * 60 * 24 * 30), // 30 days as per Cleopatra requirements
    iat: Math.floor(Date.now() / 1000)
  };
  
  return jwt.sign(payload, jwtRefreshSecret(), { algorithm: "HS256" });
}

export function verifyAccessToken(token: string): TokenPayload {
  try {
    return jwt.verify(token, jwtSecret()) as TokenPayload;
  } catch (error) {
    throw APIError.unauthenticated("Invalid access token");
  }
}

export function verifyRefreshToken(token: string): TokenPayload {
  try {
    return jwt.verify(token, jwtRefreshSecret()) as TokenPayload;
  } catch (error) {
    throw APIError.unauthenticated("Invalid refresh token");
  }
}

export function isDataModeMock(): boolean {
  return dataMode() === "MOCK";
}

export function validateEmail(email: string): boolean {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
}

export function validatePassword(password: string): boolean {
  return password.length >= 8;
}

export function generateGoogleOAuthUrl(state?: string): string {
  if (isDataModeMock()) {
    return "https://mock-google-oauth.example.com";
  }

  const params = new URLSearchParams({
    client_id: googleClientId(),
    redirect_uri: googleRedirectUri(),
    scope: "openid profile email",
    response_type: "code",
    access_type: "offline",
    prompt: "consent",
    ...(state && { state })
  });

  return `https://accounts.google.com/o/oauth2/v2/auth?${params.toString()}`;
}

export async function exchangeGoogleAuthCode(authCode: string): Promise<GoogleTokenResponse> {
  if (isDataModeMock()) {
    return {
      access_token: "mock-google-access-token",
      refresh_token: "mock-google-refresh-token",
      expires_in: 3600,
      token_type: "Bearer",
      scope: "openid profile email",
      id_token: "mock-id-token"
    };
  }

  const response = await fetch("https://oauth2.googleapis.com/token", {
    method: "POST",
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    },
    body: new URLSearchParams({
      client_id: googleClientId(),
      client_secret: googleClientSecret(),
      code: authCode,
      grant_type: "authorization_code",
      redirect_uri: googleRedirectUri()
    })
  });

  if (!response.ok) {
    throw APIError.invalidArgument("Failed to exchange authorization code");
  }

  return await response.json();
}

export async function getGoogleUserProfile(accessToken: string): Promise<GoogleUserProfile> {
  if (isDataModeMock()) {
    return {
      id: "mock-google-user-id",
      email: "user@example.com",
      verified_email: true,
      name: "Mock User",
      given_name: "Mock",
      family_name: "User",
      picture: "https://via.placeholder.com/150"
    };
  }

  const response = await fetch(`https://www.googleapis.com/oauth2/v2/userinfo?access_token=${accessToken}`);
  
  if (!response.ok) {
    throw APIError.unauthenticated("Failed to fetch user profile");
  }

  return await response.json();
}

export function generateRandomState(): string {
  return crypto.randomBytes(32).toString("hex");
}
